export default {
	data() {
		return {
			type: 1,
			newsList: [],
			pageSize: 20,
			pageIndex: 1,
			pageTotal: 1,
			nodata: false,
			nomore: false
		};
	},
	onLoad(options) {
		this.type = options.type
		let title = ''
		if (this.type == 1) {
			title = '系统消息'
		} else if (this.type == 2) {
			title = '文章消息'
		} else if (this.type == 3) {
			title = '订单消息'
		}
		uni.setNavigationBarTitle({
			title: title
		})
	},
	onShow() {
		this.getNewsList()
	},
	onPullDownRefresh() {
		this.setParams()
		this.getNewsList()
		uni.stopPullDownRefresh()
	},
	onReachBottom() {
		if (this.pageIndex < this.pageTotal) {
			this.pageIndex++
			this.getNewsList()
		} else {
			this.nomore = true
		}
	},
	methods: {
		setParams() {
			this.pageIndex = 1
			this.nomore = false
			this.nodata = false
			this.newsList = []
		},
		getNewsList() {
			let self = this
			let params = {
				fromType: this.type,
				pageIndex: this.pageIndex,
				pageSize: this.pageSize
			}
			this.$acFrame.HttpService.newsList(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if (list.length > 0) {
						list.filter(v => {
							v.createTime = self.$acFrame.Util.formatTime(v.createTime, 'day')
						})
						self.newsList = self.newsList.concat(list)
					} else {
						self.nodata = true
					}
				}
			})
		},
		setImg(src) {
			if (!src) {
				src = ''
			}
			return this.$acFrame.Util.setImgUrl(src);
		},
		mycenter(info) {
			uni.navigateTo({
				url: 'mycenter?userCode=' + info.userCode
			})
		},
		articlDetail(id) {
			uni.navigateTo({
				url: "/pages/home/commentDetail?articalId=" + id
			})
		}
	}
}
